import React, { useState } from 'react'
import { Avatar, Divider, List, Input, Popover } from 'antd'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { SearchOutlined } from '@ant-design/icons'
import style from './style.module.scss'

const Search = () => {
  const [labels, setLabels] = useState([
    {
      key: 1,
      name: 'Design',
      accent: '#51C3C4',
      checked: true,
    },
    {
      key: 2,
      name: 'Spatial Map',
      accent: '#584ABE',
    },
    {
      key: 3,
      name: 'Maintenance',
      accent: '#F1C40F',
    },
    {
      key: 4,
      name: 'Next Framework',
      accent: '#E84393',
    },
    {
      key: 5,
      name: 'React',
      accent: '#F2994A',
    },
  ])

  const [members, setMembers] = useState([
    {
      key: 1,
      avatar: '/resources/images/avatars/1.jpg',
      name: 'Dulce Ekstrom',
      checked: true,
    },
    {
      key: 2,
      avatar: '/resources/images/avatars/2.jpg',
      name: 'Tifanny Sofia',
      checked: true,
    },
    {
      key: 3,
      avatar: '/resources/images/avatars/3.jpg',
      name: 'Justin Gouse',
      checked: true,
    },
    {
      key: 4,
      avatar: '/resources/images/avatars/4.jpg',
      name: 'Jaydon Rhiel Madsen',
    },
    {
      key: 5,
      initial: 'TS',
      accent: '#F2994A',
      name: 'Tristram Elliott',
    },
    {
      key: 6,
      avatar: '/resources/images/avatars/5.jpg',
      name: 'Kelsey Gardner',
    },
    {
      key: 7,
      avatar: '/resources/images/avatars/1.jpg',
      name: 'Alec Chavez',
    },
    {
      key: 8,
      avatar: '/resources/images/avatars/2.jpg',
      name: 'Garth West',
    },
    {
      key: 9,
      avatar: '/resources/images/avatars/3.jpg',
      name: 'Dulce Ekstrom',
    },
    {
      key: 10,
      avatar: '/resources/images/avatars/4.jpg',
      name: 'Dulce Ekstrom',
    },
    {
      key: 11,
      initial: 'TS',
      accent: '#E84393',
      name: 'Dulce Ekstrom',
    },
    {
      key: 12,
      avatar: '/resources/images/avatars/5.jpg',
      name: 'Dulce Ekstrom',
    },
  ])

  const handleLabelClick = label => {
    const items = [...labels]
    const index = items.findIndex(e => e.key === label.key)
    if (index !== -1) {
      items[index] = {
        ...items[index],
        checked: !items[index].checked,
      }
      setLabels(items)
    }
  }

  const handleMemberClick = member => {
    const items = [...members]
    const index = items.findIndex(e => e.key === member.key)
    if (index !== -1) {
      items[index] = {
        ...items[index],
        checked: !items[index].checked,
      }
      setMembers(items)
    }
  }

  return (
    <Popover
      overlayClassName={style['search-popup']}
      trigger="click"
      placement="bottomLeft"
      content={
        <div className="content">
          <PerfectScrollbar>
            <div className="content-inner">
              <div className="font-weight-bold mb-3">Filter By Label</div>
              <List
                grid={{
                  column: 3,
                }}
                dataSource={labels}
                renderItem={m => (
                  <List.Item key={m.key} onClick={() => handleLabelClick(m)}>
                    <List.Item.Meta
                      avatar={
                        <Avatar
                          shape="square"
                          className={m.checked ? 'checked' : ''}
                          size={32}
                          style={{ backgroundColor: m.accent, color: '#ffffff' }}
                        >
                          {m.initial}
                        </Avatar>
                      }
                      title={m.name}
                    />
                  </List.Item>
                )}
              />
              <span>Show all labels (20 hidden)</span>
              <Divider />
              <div className="font-weight-bold mb-3">Filter By Member</div>
              <List
                grid={{
                  column: 3,
                }}
                dataSource={members}
                renderItem={m => (
                  <List.Item key={m.key} onClick={() => handleMemberClick(m)}>
                    <List.Item.Meta
                      avatar={
                        m.avatar ? (
                          <Avatar className={m.checked ? 'checked' : ''} size={32} src={m.avatar} />
                        ) : (
                          <Avatar
                            className={m.checked ? 'checked' : ''}
                            size={32}
                            style={{ backgroundColor: m.accent, color: '#ffffff' }}
                          >
                            {m.initial}
                          </Avatar>
                        )
                      }
                      title={m.name}
                    />
                  </List.Item>
                )}
              />
              <span>Show all members (10 hidden)</span>
            </div>
          </PerfectScrollbar>
        </div>
      }
    >
      <Input
        size="large"
        prefix={<SearchOutlined style={{ marginRight: 12, color: '#1358AA' }} />}
        placeholder="Search everything"
      />
    </Popover>
  )
}

export default Search
